import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AboutView from '../views/AboutView.vue'
import IsabelView from '../views/IsabelView.vue'
import CosmoView from '../views/CosmoView.vue'
import CaquiView from '../views/CaquiView.vue'
import AzucenaView from '../views/AzucenaView.vue'
import MaiteView from '../views/MaiteView.vue'
import MariposaView from '../views/MariposaView.vue'
import BbView from '../views/BbView.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: AboutView
  },
  {
    path: '/isabel',
    name: 'isabel',
    component: IsabelView
  },
  {
    path: '/cosmo',
    name: 'cosmo',
    component: CosmoView
  },
  {
    path: '/caqui',
    name: 'caqui',
    component: CaquiView
  },
  {
    path: '/azucena',
    name: 'azucena',
    component: AzucenaView
  },
  {
    path: '/maite',
    name: 'maite',
    component: MaiteView
  },
  {
    path: '/mariposa',
    name: 'mariposa',
    component: MariposaView
  },
  {
    path: '/bb',
    name: 'bb',
    component: BbView
  },
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
